<template>
    <div class="bindname">
      <div class="input-section">
          <p class="tip">请输入新名称</p>
          <div class="input-area">
            <input @textInput="textInutCheck($event)" class="name-text" type="text" ref="nameText" @focus="nameTextFocusHandle" @blur="nameTextBlurHandle" v-model="nameTextValue" :placeholder="username" >
            <div v-show="showClear" ref="clearBtn" @click="clearTextHandle" class="text-clear-div"><span class="iconfont icon-close"></span></div>
          </div>
      </div>
      <mt-button size="large" class="commit-btn" type="default" @click.native="commitHandle">提交</mt-button>
    </div>
</template>
<script>
import { mapState } from "vuex";
import BScroll from "better-scroll";
import Vue from "vue";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/toast/style.css";
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);

export default {
  name: "bind-name",
  data() {
    return {
      nameTextValue: "",
      showClear: false,
      clearId:-1000
    };
  },
  watch: {
    nameTextValue(val) {
      if (!val) this.showClear = false;
    }
  },
  mounted() {
    this.$store.dispatch("toggleLoading", false);
  },
  computed: {
    ...mapState(["username", "path", "userid"])
  },
  beforeRouteLeave(to, from, next) {
    // this.$store.commit("UPDATE_NOBINDSHOW", false);
    if (this.clearId != -1000) {
      clearTimeout(this.clearId);
      this.clearId=-1000;
    }
    next();
  },
  methods: {
    textInutCheck( event) {
      if (this.nameTextValue.length >= 11) event.returnValue = false;
      else this.showClear = true;
    },
    nameTextFocusHandle() {
      if (this.nameTextValue) this.showClear = true;
    },
    nameTextBlurHandle() {
      this.showClear = false;
    },
    clearTextHandle(event) {
      this.nameTextValue = "";
      this.$refs.nameText.focus();
    },
    commitHandle: function() {
      if(!this.nameTextValue) return;
      if(this.nameTextValue==this.username){
        this.nameTextValue="";
        return;
      }
      let tempStr=this.nameTextValue.replace(/(^\s*)|(\s*$)/g, "");
      if(tempStr==""){
        this.nameTextValue="";
        return;
      }
      this.commitRequest();
    },
    commitRequest() {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/suncode/dealfuc.php", {
          fuc: "editinfo",
          user: this.userid,
          username: this.nameTextValue
        })
        .then(response => {
          this.$store.dispatch("toggleLoading", false);
          var respdata = response.body;
          if (respdata.status == "200") {
            this.$store.commit("UPDATE_USERNAME", this.nameTextValue);
            Toast({
              message: "修改成功",
              iconClass: "iconfont icon-tishi1",
              duration: 1000
            });
            this.clearId=setTimeout(()=>{this.$router.go(-1)},800);
          } else
            Toast({
              message: "修改失败",
              iconClass: "iconfont icon-tishi1",
              duration: 1000
            });
        })
        .catch(response => {
          this.$store.dispatch("toggleLoading", false);
          console.log(response);
        });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>

.bindname {
  padding-top: 0.3rem;

  .input-section {
    margin-top: 0.5rem;
    width: 100%;
    text-align center;

    .tip {
      font-size: 0.13rem;
      color: #222;
      letter-spacing: 0.01rem;
    }

    .input-area {
      margin-top: 0.1rem;
      width 62%;
      height 0.32rem;
      position: relative;
      display inline-block;
      border-bottom: 1px #dedede solid;

      input {
        width: 100%;
        -webkit-appearance: none; // 去掉iname背景阴影
        border: none;
        outline: none;
        box-sizing: border-box;
        -webkit-touch-callout: none;
        color: #333;
        font-size: 0.14rem;
        top: -0.01rem;
        letter-spacing: 0.005rem;
        height 0.32rem;
        padding-left .12rem;

        &::-webkit-input-placeholder {
          color: #ddd;
          font-size: 0.13rem;
          letter-spacing: 0.01rem;
        }
      }

      .text-clear-div {
        position: absolute;
        right: 0px;
        top: 50%;
        transform: translate(-50%, -50%);

        span {
          font-size: 0.16rem;
          color: #8a8a8a;
        }
      }
    }
  }

  .commit-btn {
    background-color: #f8ef94;
    color: #a2733d;
    margin-top: 0.4rem;
    margin-left: 0.4rem;
    width: calc(100% - .8rem);
    font-size: 0.12rem;
    letter-spacing: 0.02rem;
    height: 0.3rem;
    box-shadow: none;
  }
}
</style>





